/* App.css */
.App {
  text-align: center;
  background-color: #f0f0f0;
  font-family: 'Courier New', Courier, monospace, sans-serif;
  padding: 20px;
}

h1 {
  font-family: 'Bokor';
  font-size: 80px;
  
}

nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  background-color: #270c51;
  padding: 10px;
}

nav a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bold;
}

nav a:hover {
  text-decoration: underline;
}

